body {
	display: flex;
	justify-content: center;
}

.slotContainer {
	max-width: 800px;
	width: 95vw;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-top: 20px;
}

.slot {
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: space-between;
}

.heroBadge {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	background-color: lightgray;
	background-size: contain;
	border: 1px solid darkgray;

	&.locked {
		cursor: default;
		filter: opacity(20%);
	}

	.heroBadgeChainContainer {
		background-color: rgba(255, 255, 255, 0.5);
		border-top-left-radius: 10px;
		align-self: flex-end;
		margin-left: auto;
		padding-bottom: 3px;
		padding-right: 3px;
	}

	.heroBadgeElementRoleContainer {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-self: flex-start;
		height: inherit;
		img {
			width: inherit;
			&:nth-child(1) {
				border-top-left-radius: 10px;
				border-bottom-right-radius: 5px;
			}
			&:nth-child(2) {
				border-bottom-left-radius: 10px;
				border-top-right-radius: 5px;
			}
		}
	}
}

.weaponBadge {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 10px;
	border: 1px solid darkgray;
	background-color: lightgray;
	background-size: contain;

	.weaponBadgeAilment {
		align-self: flex-end;
		margin-left: auto;
		padding-right: 2px;
	}
}

.chainIcon {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	:nth-child(1) {
		position: absolute;
		top: 2px;
		left: 2px;
	}
	:nth-child(2) {
		position: absolute;
		bottom: 0;
		right: 0;
	}
	:nth-child(3) {
		position: absolute;
	}
}

.heroPicker {
	display: flex;
	max-width: 80vf;
	flex-wrap: wrap;

	.badgeWrapper {
		margin: 10px;
	}
}

.weaponPicker {
	display: flex;
	max-width: 80vf;
	flex-wrap: wrap;

	.badgeWrapper {
		margin: 10px;
	}
}

.partyBuffSummary {
	border: 1px solid lightgray;
	border-radius: 5px;
	padding: 10px;
	line-height: 25px;
	margin-top: 30px;
}

.chainInfo {
	border: 1px solid lightgray;
	border-radius: 5px;
	padding: 10px;
	line-height: 25px;
	margin-top: 30px;

	.chain {
		display: flex;
		align-items: center;
		margin: 10px 0;

		.chainArrow {
			font-size: 30px;
			margin: 0 10px;
		}
	}
}

.resetButton {
	margin-top: 30px;
	width: 80px;
	height: 30px;
}
